:root {
  --navy: #193980;
  --lightblue: #27ccd8;
  --gray: #645c5c;
}

.about {
  display: grid;
  grid-template-columns: auto 1fr;
  grid-template-rows: minmax(1fr, 100px) 1fr;
  border: 1px solid red;
  margin-top: 150px;
  height: auto;
}

.title {
  grid-column: 1;
  grid-row: 1;
  font-size: 100px;
  font-weight: 800;
  background: linear-gradient(var(--navy), var(--lightblue));
  background-clip: text;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -moz-text-fill-color: transparent;
  -webkit-text-fill-color: transparent;
  text-align: flex-start;
  margin-bottom: 50px;
  border: 1px solid blue;
}

.text {
  grid-column: 1;
  grid-row: 2;
  font-size: 22px;
  color: var(--gray);
  padding-right: 15px;
  border: 1px solid blue;
}

.text p {
  margin-bottom: 12px;
}

br {
  line-height: 20%;
  border: 1px solid green;
}

.profile-pic {
  grid-column: 2;
  grid-row: span 2;
  border: 1px solid red;
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile-pic img {
  width: 300px;
  border: 1px solid blue;
}

/*  */
/* MEDIA QUERIES */
/*  */
@media (max-width: 1224px) {
  .title {
    font-size: 85px;
  }

  .text {
    font-size: 20px;
  }

  .profile-pic img {
    width: 230px;
  }
}

