/* @media (max-width: 768px) */

@media (max-width: 1024px) {
  .about {
    display: grid;
    grid-template-columns: 1;
    grid-template-rows: 1fr auto 2fr;
    margin-top: 30px;
  }

  .title {
    grid-column: 1;
    grid-row: 1;
    font-size: 55px;
    margin-bottom: 25px;
    text-align: center;
  }

  .text {
    grid-column: 1;
    grid-row: 3;
    font-size: 18px;
  }

  .profile-pic {
    grid-column: 1;
    grid-row: 2;
    margin-bottom: 25px;
  }

  .profile-pic img {
    width: 250px;
  }
}

@media (max-width: 480px) {
  .title {
    font-size: 50px;
  }

  .text {
    font-size: 15px;
  }

  .profile-pic img {
    width: 230px;
  }
}
